<template>
	<div class="saleedi-authPage">
	</div>
</template>

<script>
export default {
	name: 'authPage',
	data () {
		return {
		}
	},
	created () {
		var params = window.location.search.replace(/^\?/, '')
		params = params.split('&')
		var code = params[0].split('=')[1]
		this.$fxHttp.requestPost({
			url: `${this.$fxBaseApi.urlA}/authRouter`,
			data: {
				code
			}
		}).then(res => {
			if (res.authFlag === '0') {
				this.$router.replace('/login')
			} else if (res.authFlag === '1') {
				this.$router.replace('/bindEnt')
			} else {
				this.$router.replace('/home/find')
			}
		}, error => {
			console.log(error)
			this.$router.replace('/home/find')
		})
	}
}
</script>
